<template>
  <v-app>
    <v-app-bar 
      app
      elevation="0"
      >
      <v-app-bar-nav-icon v-if="globalWidth<600" @click="openCloseDrawer"></v-app-bar-nav-icon>
      <v-toolbar-title class="headline text-uppercase">
        <span>{{this.$route.name}}</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-navigation-drawer
      :expand-on-hover="isOpen"
      v-model="isActive"
      disable-resize-watcher="true"
      disable-route-watcher="true"
      :permanent="isOpen"
      dark
      height="100%"
      app
    >
      <v-list rounded>
        <v-list-item>
          <v-list-item-avatar>
            <v-img src="https://randomuser.me/api/portraits/men/85.jpg"></v-img>
          </v-list-item-avatar>
        </v-list-item>
        <v-list-item
          two-line>
          <v-list-item-content>
            <v-list-item-title>{{userInfo.userName}}</v-list-item-title>
            <v-list-item-subtitle>{{userInfo.userEmail}}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          link
          :to="item.link"
          active-class="pink white--text"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-content app>
      <transition>
        <router-view/>
      </transition>
    </v-content>
  </v-app>
</template>

<script>
import StartPage from './components/StartPage'
import UsersPage from './components/UsersPage'
import TemplatesPage from './components/TemplatesPage'
import Vue from 'vue'
import VueRouter from 'vue-router'
import { isObject } from 'util';

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {path: '/', redirect:'Inicio'},
    {path: '/Inicio', component: StartPage,name:'Inicio'},
    {path: '/Usuarios',component: UsersPage,name:'Usuarios'},
    {path: '/Plantillas',component: TemplatesPage,name:'Plantillas'}
  ]
})

export default {
  name: 'App',
  components: {
    StartPage,
    UsersPage,
    TemplatesPage
  },
  data: () => ({
    //appBarTitle:String,
    userInfo:{
      userName: "Marco Vinicio",
      userEmail: "vinicio00097@gmail.com",
    },
    items: [
      { title: 'Inicio', icon: 'home',link:'/Inicio' },
      { title: 'Usuarios', icon: 'people' ,link:'/Usuarios'},
      { title: 'Plantillas', icon: 'grid_on' ,link:'/Plantillas'},
    ],
    globalWidth:0,
    isOpen:true,
    isActive:true,
  }),
  created() {
    this.globalWidth=window.innerWidth;
    if(this.globalWidth<650){
      this.isOpen=false;
    }else{
      this.isOpen=true;
    }
  },
  destroyed() {
    
  },
  beforeCreate(){
    this.$vuetify.theme.dark=true;
    this.$vuetify.hola="hola";
  },
  methods: {
    openCloseDrawer(){
      if(this.isActive){
        this.isActive=!this.isActive
      }else{
        this.isActive=!this.isActive
      }
    }
  },
  mounted() {
    window.onresize = () => {
      this.globalWidth=window.innerWidth;
      if(this.globalWidth<650){
        this.isOpen=false;
      }else{
        this.isOpen=true;
      }
    }
  },
  /*watch:{
    $route ($to,$from){
      this.appBarTitle=$to.name;
    }
  },*/
  router: router
};
</script>
